@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap";
.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding-inline: 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 1px 6px #f3f3f3;
}

.logo {
  display: flex;
}

.header__navigation, .header__navigation ul, .header__navigation a {
  height: 100%;
}

.navigation ul {
  gap: 20px;
  display: flex;
}

.navigation ul .navigation__link {
  align-items: center;
  display: flex;
}

.navigation ul .navigation__link.is-active {
  color: #00acdc;
  position: relative;
}

.navigation ul .navigation__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.catalog {
  grid-template-rows: auto;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__description {
  margin-bottom: 4px;
  margin-top: unset;
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__subinfo {
  color: #616070;
  align-self: flex-start;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.card__rate .rate__stars {
  gap: 4px;
  display: flex;
}

.card__rate .rate__stars svg {
  width: 16px;
  height: 16px;
}

.card__rate .rate__reviews {
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.card__price_hint {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price_amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card:hover {
  scale: 1.2;
}

.card:hover .card__description {
  color: #34568b;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

ul {
  margin: unset;
  padding: unset;
  list-style-type: none;
}

a {
  color: #060b35;
  text-decoration: none;
  transition: all .3s;
}

a:hover {
  color: #00acdc;
}

.button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 11px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.button:hover {
  background-color: inherit;
  color: #00acdc;
}
